import React, { useState, useContext } from "react";
import {Link} from 'react-router-dom';
import { DataContext } from "./Data";
import countries_list from "./CountriesList.json";

function Navbar() {
  const { countries, categories } = React.useContext(DataContext);
  const [selected_country, setCountry] = countries;
  const [selected_category, setCategory] = categories;
  return (
    <header>
      <div className="container">
        <nav className="navbar navbar-expand-lg navbar-light">
          <Link className="navbar-brand" to="/">
            Home
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto">
              <li className="nav-item">
                <Link
                  className="nav-link"
                  to={`/top-headlines/${selected_country.code}`}
                >
                  Top Headlines
                </Link>
              </li>
              <li className="nav-item dropdown">
                <Link
                  className="nav-link dropdown-toggle"
                  to="#"
                  id="navbarDropdown"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  Select Country
                </Link>
                <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                {countries_list && countries_list.length > 0 && countries_list.map((country, index) => (
                    <div key={index}>
                      <Link
                        className="dropdown-item"
                        to={`/top-headlines/${country.code}`}
                      >
                        {country.name}
                      </Link>
                      <div className="dropdown-divider"></div>
                    </div>
                  ))}
                </div>
              </li>
            </ul>
            <div className="d-inline-flex my-2 my-lg-0">
              <Link to="#">
                <i className="fa fa-cog" aria-hidden="true"></i>
              </Link>
            </div>
          </div>
        </nav>
      </div>
    </header>
  );
}

export default Navbar;
