import React, { useState, useEffect, useContext } from "react";
import { useParams } from "react-router-dom";
import { DataContext } from "./Data";
import countries_list from "./CountriesList.json";

function TopHeadlines() {
  const {
    api_host,
    api_key,
    countries,
    categories,
    news_list,
  } = React.useContext(DataContext);
  const [selected_country, setCountry] = countries;
  const [selected_category, setCategory] = categories;
  var [newsList, setNews] = news_list;
  var path_country_name = selected_country.name;
//var path_country_code = window.location.pathname.split("/");
  let slug = useParams();
  var path_country_code = slug && slug.country_id || 'in' ;
  console.log(path_country_code);
  useEffect(() => {
    const getData = async () => {
      const api_url = `${api_host}/top-headlines/category/${selected_category}/${path_country_code}.json`;
      const response = await fetch(api_url);
      const data = await response.json();
      console.log(data);
      setNews(data.articles);
    };
    getData();
  }, [slug]);

  countries_list && countries_list.length > 0 && countries_list.map((country) => {
    if (country.code == path_country_code) {
      path_country_name = country.name;
    }
  });
  var newsList1, newsList2;
  {newsList1 = newsList.slice(1, 4)}
  {newsList2 = newsList.slice(4, 10)}
  return (
    <>
      <div className="container">
        <div className="headlines_title">
          Top Headlines from {path_country_name}
        </div>
        {
          <div className="headlines row">
            {newsList1 && newsList1.length > 0 && newsList1.map((news, index) => (
              <div
                key={index}
                className="hd-col col-lg-3 col-md-3 col-sm-6 col-xs-12"
              >
                <div className="hd-card card">
                {index <=2 ? (<img src={news.urlToImage} alt={news.source} />) : 
                ( <></> )}
                  <div className="card-body">
                    <a href="#" className="card-title">
                      {news.title}
                    </a>
                  </div>
                </div>
              </div>
            ))}
          </div>
        }
        {
          <div className="headlines2">
            {newsList2 && newsList2.length > 0 && newsList2.map((news, index) => (
              <div key={index} >
                <div className="title">
                    <a href="#" >{news.title}</a>
                </div>
              </div>
            ))}
          </div>
        }
      </div>
    </>
  );
}

export default TopHeadlines;
