import React, { useState, createContext } from "react";
import countries_list from "./CountriesList.json";

export const DataContext = createContext();
export const DataProvider = (props) => {
  const api_host = "https://saurav.tech/NewsAPI";
  const api_key = "0b1aa3a797ef4fa48ea7bf7e7bb59690";
  const [newsList, setNews] = useState([]);
  const [selected_country, setCountry] = useState({
    name: "India",
    code: "in",
  });
  const [selected_category, setCategory] = useState("health");

  return (
    <DataContext.Provider
      value={{
        api_host: api_host,
        api_key: api_key,
        countries: [selected_country, setCountry],
        categories: [selected_category, setCategory],
        news_list: [newsList, setNews],
      }}
    >
      {props.children}
    </DataContext.Provider>
  );
};
